@import 'manifesto-brand/manifesto-brand';

.with-red-bold {
  b {
    color: $color-primary-red;
  }
}

#slide-1 {
  background: url("../images/nelson_haha.jpg") no-repeat bottom right #FFF;
}

#confused {
  background: url("../images/urlo.jpg") no-repeat left center;
}

#common-ways-2 {
  background: url("../images/fail.gif") no-repeat center bottom #FFF;
}

#common-ways-3 {
  img {
    max-width: 100%;
    margin-top: 2em;
  }
}

#common-ways-4 {
  li {
    font-size: 1.25em;
    margin-top: 1.8em;
  }
}

#drush-make {
  display: block;
  position: relative;
  &::after {
    content: "";
    background-image: url("../images/One_Ring_Render.png");
    background-size: cover;
    opacity: 0.12;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
  }

}

#drush-make-definition {
  code {
    margin-top: 1.5em;
  }
}

#drush-make-description {
  h3 {
    margin-bottom: 0.25em;
    margin-top: 1em;
  }
  li {
    margin-bottom: 0.25em;
  }
}

#drush-make-file {
  h1 {
    margin-top: 30%;
  }
}
