body {
  font-family: $font-primary-regular, sans-serif;
  min-height: 740px;

  background: rgb(34, 34, 34);
  background: -webkit-gradient(radial, 50% 50%, 0, 50% 50%, 500, from(rgb(60, 60, 60)), to(rgb(8, 8, 8)));
  background: -webkit-radial-gradient(rgb(60, 60, 60), rgb(8, 8, 8));
  background:    -moz-radial-gradient(rgb(60, 60, 60), rgb(8, 8, 8));
  background:     -ms-radial-gradient(rgb(60, 60, 60), rgb(8, 8, 8));
  background:      -o-radial-gradient(rgb(60, 60, 60), rgb(8, 8, 8));
  background:         radial-gradient(rgb(60, 60, 60), rgb(8, 8, 8));
}

.fallback-message {
  font-family: sans-serif;
  line-height: 1.3;

  width: 780px;
  padding: 10px 10px 0;
  margin: 20px auto;

  border: 1px solid #E4C652;
  border-radius: 10px;
  background: #EEDC94;

  p {
    margin-bottom: 10px;
  }

  .impress-enabled & {
    display: none;
  }

}


.step {
  position: relative;
  width: $slide-width;
  padding: $slide-padding;
  margin: 20px auto;
  overflow: hidden;

  -webkit-box-sizing: border-box;
  -moz-box-sizing:    border-box;
  -ms-box-sizing:     border-box;
  -o-box-sizing:      border-box;
  box-sizing:         border-box;

  .impress-enabled & {
    margin: 0;
    opacity: 0;

    &.past {
      opacity: 0.3;
    }

    -webkit-transition: opacity 1s;
    -moz-transition:    opacity 1s;
    -ms-transition:     opacity 1s;
    -o-transition:      opacity 1s;
    transition:         opacity 1s;

    &.active {
      opacity: 1
    }
  }
}

.slide {
  display: block;

  width: $slide-width;
  height: $slide-height;
  padding: $slide-padding-top $slide-padding-left;

  background-color: white;
  border: 1px solid rgba(0, 0, 0, .3);
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, .1);

  color: rgb(102, 102, 102);
  text-shadow: 0 2px 2px rgba(0, 0, 0, .1);

  font-size: 1.6em;
  line-height: 2;

  &.slide--type-default {
    border-top: 20px solid $color-primary-blue;
    border-bottom:  20px solid $color-primary-blue;
    padding: ($slide-padding-top - 20px) $slide-padding-left;
  }

  &:not(.slide--type-default)[class*=slide--type-] {
    background-color: #000;
    text-align: center;

    &,h1,h2,h3,h4,h5,a,p,q {
      color: #fff;
    }

    &.slide--type-red {
      background-color: $color-primary-red;
    }

    &.slide--type-blue {
      background-color: $color-primary-blue;
    }

    &.slide--type-yellow {
      background-color: $color-primary-yellow;
      &,h1,h2,h3,h4,h5,a,p,q {
        color: $color-primary-black;
      }
    }

  }

}

code.terminal:before {
  content: '$ ';
}
code.terminal:after {
  content:"_";
  opacity: 0;
  animation: cursor 1s infinite;
}
@keyframes cursor {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
