@mixin h1-branded {
  font-family: $font-primary-regular;
  font-size: 2.77em;
  line-height: 1.25;
}

@mixin h2-branded {
  font-family: $font-primary-regular;
  font-size: 1.66em;
  font-weight: 100;
  color: $color-secondary-black;
  line-height: 1.125;
}

@mixin h3-branded {
  font-family: $font-primary-regular;
  font-size: 1.55em;
  font-weight: bold;
  line-height: 1;
}

@mixin h4-branded {
  font-family: $font-primary-regular;
  font-weight: bold;
  font-size: 1.16em;
  color: $color-primary-red;
  line-height: 1;
}


body {
  font-family: $font-primary-regular;
  font-size: $font-size;
  color: $color-primary-black;
  line-height: 1.6;
}

h1 {
  @include h1-branded;
  margin-bottom: 1em;
  margin-top: 0.5em;
}

h2 {
  @include h2-branded;
  margin-bottom: 0.75em;
  &:first-child {
    margin-top: 0.25em;
  }
}

h3 {
  @include h3-branded;
  margin-bottom: 0.5em;
}

h4 {
  @include h4-branded;
  margin-bottom: 0.5em;
}

b, strong { font-weight: bold }
i, em { font-style: italic }

a {
  color: inherit;
  text-decoration: none;
  padding: 0 0.1em;
  background: rgba(255,255,255,0.5);
  text-shadow: -1px -1px 2px rgba(100,100,100,0.9);
  border-radius: 0.2em;
  transition:         0.5s;
}

a:hover,
a:focus {
  background: rgba(255,255,255,1);
  text-shadow: -1px -1px 2px rgba(100,100,100,0.5);
}


blockquote {
  font-family: $font-primary-regular;
  font-size: 18px;
  color: $color-primary-blue;
}

li {
  list-style: disc inside;
  line-height: 1.2;
  margin-bottom: 0.8em;
  ul {
    margin-top: 0.8em;
  }
  li {
    list-style: circle inside;
    padding-left: 2em;
  }
}

.slide q {
  display: block;
  font-size: 2em;
  line-height: 1.5;
  margin-top: 20%;
}

code {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: 0.8em;
  line-height: 140%;
  white-space: pre;
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -o-pre-wrap;
  background: $color-primary-grey;
  display: block;
  border: 1px solid $color-primary-black;
  padding: 1em;
  margin-bottom: 0.5em;
  margin-top: 1em;
}

.centralize {
  text-align: center;
}
