// ===== Color =====
$color-primary-red: #ff5b5c;
$color-primary-blue: #06bfc0 ;
$color-primary-yellow: #ffea00;
$color-primary-black: #222222;
$color-primary-grey: #efefef;

$color-secondary-red: #ff737a;
$color-secondary-blue: #1cd3ce ;
$color-secondary-yellow: #fff064;
$color-secondary-black: #4d555b;


// ===== Fonts =====
$font-primary-regular: 'museo_sans300';
$font-primary-light: 'museo_sans100';
$font-primary-bold: 'museo_sans500';
$font-primary-bolder: 'museo_sans700';
$font-primary-boldest: 'museo_sans900';

$font-size: 22px;


// ===== Layout (defaults) ====

$slide-width: 900px;
$slide-height: 700px;
$slide-gutter: 100px;
$slide-padding: 40px;
$slide-padding-top: $slide-padding;//top-bottom
$slide-padding-left: 60px;//left-right

// ==== Layout big/auto ====
$font-size: 1.5vh;
$slide-width: 90vh;
$slide-height: 80vh;
$slide-gutter: 20vh;
$slide-padding: 60px;
$slide-padding-top: $slide-padding;//top-bottom
$slide-padding-left: 90px;//left-right
